body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}
.container {
  display: flex;
  justify-content: center;
  padding: 10px;
}
.console-output {
  overflow: scroll;
  width: 100%;
  height: 100%;
}
.input-prompt {
  width: 100%;
}
.get-image {
  height: 640px;
  width: 360px;
}
.console {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: flex-start;
  width: 100%;
  padding: 10px;
}
.prompt-text {
  color: lawngreen;
}
.file-input-prompt {
  width: 5%;
  visibility:hidden;
}
input {
  color: white;
  background-color: black;
  border: none;
  outline: none;
  width: 80%;
  font-size: 1em;
}
